<template>
  <v-container fluid>
    <PageTitle title="線上報名" />
    <ProgressCircular v-if="!signup"/>
    <div v-else >
      <ContentText
        :content="signup.timeline"
        align="center"
        :blod="false"
      />

      <v-row class="mx-auto ma-8">
        <v-col v-for="group in signup.groups" :key="group.id">
          <Card
            :image="group.image"
            :cardTitle="group.name"
            :cardInfo="
              group.name !== '照片上傳' ? [
                { type: 'divider' },
                { type: 'button', text: signup.status, icon: 'mdi-login', disable: signup.status !== '立即報名' ? true : false, link: group.signup_url },
                { type: 'button', text: '查詢結果', icon: 'mdi-magnify', disable: signup.status === '尚未開放' ? true : false, link: group.check_url }
              ] : [
                { type: 'divider' },
                { type: 'button', text: signup.status === '尚未開放' ? signup.status : '立即上傳', icon: 'mdi-upload', disable: signup.status === '尚未開放' ? true : false, link: group.signup_url },
                { type: 'button', text: '查詢結果', icon: 'mdi-magnify', disable: signup.status === '尚未開放' ? true : false, link: group.check_url }
              ]
          "/>
        </v-col>
      </v-row>
    </div>
  </v-container>
</template>

<script>
import api from '@/lib/api';

export default {
  name: 'Signup',

  data () {
    return {
      signup: null
    };
  },

  beforeMount () {
    this.init();
  },

  methods: {
    init: async function () {
      this.signup = await api.get('signup');
    }
  }
};
</script>
