<template>
  <v-container fluid>
    <PageTitle :title="typeMap[type]" />
    <ProgressCircular v-if="!qas"/>
    <div v-else>
      <CardText
        v-for="qa in qas" :key="qa.id"
        :imageIcon="'mdi-help-box'"
        :cardTitle="qa.question"
        :cardMainText="qa.answer"
        cardSubText=""
      />
    </div>
  </v-container>
</template>

<script>
import api from '@/lib/api';

export default {
  name: 'Qa',

  data () {
    return {
      typeMap: {
        signup: '關於報名',
        competition: '關於比賽',
        store: '關於商品'
      },
      qas: null
    };
  },

  props: {
    type: {
      type: String,
      required: true
    }
  },

  watch: {
    type: function () {
      this.init();
    }
  },

  beforeMount () {
    this.init();
  },

  methods: {
    init: async function () {
      this.qas = null;
      const allQas = (await api.get('qa'));
      const qas = [];

      for (const qa of allQas) {
        if (qa.type === this.type) {
          qas.push(qa);
        }
      }

      this.qas = qas;
    }
  }
};
</script>
