<template>
  <v-container fluid>
    <PageTitle title="歷史沿革" />
    <ProgressCircular v-if="!history"/>
    <div v-else>
      <ContentText
        :content="history.introduction"
        align="center"
        :blod="false"
      />

      <v-timeline class="ma-4" :dense="dense">
        <v-timeline-item v-for="event in history.events" :key="event.id" :color="color.panel.blue" icon="mdi-volleyball" large>
          <v-card>
            <v-card-title class="justify-center align-center">
              <v-icon size="24" left :color="color.panel.blue">mdi-calendar</v-icon>
              <div class="text-h6">{{ event.date }}</div>
            </v-card-title>

            <v-card-title class="justify-center align-center">{{ event.title }}</v-card-title>

            <v-img
              class="mx-auto ma-4"
              :max-width="event.image === 'facebook' ? 120 : 220"
              :src="event.image"
            />

            <v-card-text class="text-center">
              <div class="ma-2" v-for="line in event.content.split('\n')" :key="line.id">{{ line }}<br></div>
            </v-card-text>
          </v-card>

        </v-timeline-item>
      </v-timeline>
    </div>
  </v-container>
</template>

<script>
import api from '@/lib/api';
import color from '@/lib/color';

export default {
  name: 'History',

  data () {
    return {
      color: color,
      history: null
    };
  },

  computed: {
    dense () {
      let dense = false;

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          dense = true;
          break;
        case 'sm':
          dense = true;
          break;
        case 'md':
          dense = false;
          break;
        case 'lg':
          dense = false;
          break;
        case 'xl':
          dense = false;
          break;
      }

      return dense;
    }
  },

  beforeMount () {
    this.init();
  },

  methods: {
    init: async function () {
      this.history = await api.get('history');
    }
  }
};
</script>
