<template>
  <v-container fluid>
    <PageTitle title="主辦單位" />
    <ProgressCircular v-if="!organizers"/>
    <div v-else v-for="organizer in organizers" :key="organizer.id">
      <SectionTitle class="mx-8" :title="organizer.group" align="left" :textColor="color.text.blue" />
      <ContentText class="mx-12" :content="organizer.name" align="left" :blod="true" :textColor="color.text.orange" />
      <ContentText class="mx-16" :content="organizer.responsibility" align="left" :blod="false" />
      <v-img class="mx-16" :max-width="width" :src="organizer.image || 'https://i.imgur.com/5w75TQD.png'" />
    </div>
  </v-container>
</template>

<script>
import api from '@/lib/api';
import color from '@/lib/color';

export default {
  name: 'Organizer',

  data () {
    return {
      color: color,
      organizers: null
    };
  },

  beforeMount () {
    this.init();
  },

  computed: {
    width () {
      let width = '';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          width = '40%';
          break;
        case 'sm':
          width = '40%';
          break;
        case 'md':
          width = '20%';
          break;
        case 'lg':
          width = '20%';
          break;
        case 'xl':
          width = '20%';
          break;
      }

      return width;
    }
  },

  methods: {
    init: async function () {
      this.organizers = await api.get('organizer');
    }
  }
};
</script>
