<template>
  <v-container fluid>
    <PageTitle title="競賽章程" />
    <ProgressCircular v-if="!charter"/>
    <div v-else v-for="line in charter" :key="line.id">
      <SectionTitle v-if="line.priority === 1" :class="margin(8)" :title="line.content" align="left" :textColor="color.text.blue" />
      <ContentText v-if="line.priority === 2" :class="margin(12)" :content="line.content" align="left" :blod="true" :textColor="color.text.orange" />
      <ContentText v-if="line.priority === 3" :class="margin(16)" :content="line.content" align="left" :blod="false" />
      <ContentText v-if="line.priority === 'link'" :class="margin(16)" :content="line.content" align="left" :blod="false" :link="line.url"/>
    </div>
  </v-container>
</template>

<script>
import api from '@/lib/api';
import color from '@/lib/color';

export default {
  name: 'Charter',

  data () {
    return {
      color: color,
      charter: null
    };
  },

  beforeMount () {
    this.init();
  },

  methods: {
    init: async function () {
      this.charter = await api.get('charter');
    },
    margin (px) {
      let margin = '';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          margin = `mx-${px - 4}`;
          break;
        case 'sm':
          margin = `mx-${px - 4}`;
          break;
        case 'md':
          margin = `mx-${px}`;
          break;
        case 'lg':
          margin = `mx-${px}`;
          break;
        case 'xl':
          margin = `mx-${px}`;
          break;
      }

      return margin;
    }
  }
};
</script>
