import Vue from 'vue';
import VueRouter from 'vue-router';
import vuetify from './plugins/vuetify';

import App from '@/App.vue';
import Charter from '@/components/Charter';
import Competition from '@/components/Competition';
import Cooperation from '@/components/Cooperation';
import History from '@/components/History';
import Home from '@/components/Home';
import NotFound from '@/components/NotFound';
import Organizer from '@/components/Organizer';
import Qa from '@/components/Qa';
import Signup from '@/components/Signup';
import Store from '@/components/Store';

import Button from '@/components/material/Button';
import Card from '@/components/material/Card';
import CardLink from '@/components/material/CardLink';
import CardText from '@/components/material/CardText';
import ContentText from '@/components/material/ContentText';
import Divider from '@/components/material/Divider';
import PageTitle from '@/components/material/PageTitle';
import ProgressCircular from '@/components/material/ProgressCircular';
import SectionTitle from '@/components/material/SectionTitle';

Vue.config.productionTip = false;
Vue.config.devtools = false;

Vue.use(VueRouter);

Vue.component('Button', Button);
Vue.component('Card', Card);
Vue.component('CardLink', CardLink);
Vue.component('CardText', CardText);
Vue.component('ContentText', ContentText);
Vue.component('Divider', Divider);
Vue.component('PageTitle', PageTitle);
Vue.component('ProgressCircular', ProgressCircular);
Vue.component('SectionTitle', SectionTitle);

const router = new VueRouter({
  base: '/',
  mode: 'hash',
  scrollBehavior () {
    return { x: 0, y: 0 };
  },
  routes: [
    { path: '*', name: 'NotFound', component: NotFound },
    { path: '/', name: 'Home', component: Home },
    { path: '/home', redirect: '/' },
    { path: '/history', name: 'History', component: History },
    { path: '/charter', name: 'Charter', component: Charter },
    { path: '/signup', name: 'Signup', component: Signup },
    { path: '/store', name: 'Store', component: Store },
    { path: '/competition', name: 'Competition', component: Competition },
    { path: '/qa/:type', name: 'Qa', component: Qa, props: true },
    { path: '/organizer', name: 'Organizer', component: Organizer },
    { path: '/cooperation', name: 'Cooperation', component: Cooperation }
  ]
});

new Vue({
  vuetify,
  router,
  render: h => h(App)
}).$mount('#app');
