<template>
  <v-container fluid>
    <PageTitle title="商品專區" />
    <ProgressCircular v-if="!store"/>
    <div v-else>
      <ContentText
        :content="store.timeline"
        align="center"
        :blod="false"
      />

      <v-row class="mx-auto ma-8">
        <v-col v-for="group in store.groups" :key="group.id">
          <Card
            :image="group.image"
            :cardTitle="group.name"
            :cardInfo="
              group.name !== '匯款資訊上傳' ? [
                { type: 'divider' },
                { type: 'button', text: store.status, icon: 'mdi-login', disable: store.status !== '立即購買' ? true : false, link: group.buy_url },
                { type: 'button', text: '查詢結果', icon: 'mdi-magnify', disable: store.status === '尚未開放' ? true : false, link: group.check_url }
              ] : [
                { type: 'divider' },
                { type: 'button', text: store.status === '尚未開放' ? store.status : '立即上傳', icon: 'mdi-upload', disable: store.status === '尚未開放' ? true : false, link: group.buy_url },
              ]
            "/>
        </v-col>
      </v-row>
    </div>
  </v-container>
</template>

<script>
import api from '@/lib/api';

export default {
  name: 'Store',

  data () {
    return {
      store: null
    };
  },

  beforeMount () {
    this.init();
  },

  methods: {
    init: async function () {
      this.store = await api.get('store');
    }
  }
};
</script>
