<template>
  <v-container fluid>
    <PageTitle title="賽事公告" />
    <ProgressCircular v-if="!competitions"/>
    <div v-else v-for="competition in competitions" :key="competition.id">
      <SectionTitle :title="competition.title" align="center" />
      <v-img class="mx-auto mx-16" :max-width="width" :src="competition.image_url" />
    </div>
  </v-container>
</template>

<script>
import api from '@/lib/api';

export default {
  name: 'Competition',

  data () {
    return {
      competitions: null
    };
  },

  computed: {
    width () {
      let width = '';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          width = '90%';
          break;
        case 'sm':
          width = '90%';
          break;
        case 'md':
          width = '60%';
          break;
        case 'lg':
          width = '60%';
          break;
        case 'xl':
          width = '60%%';
          break;
      }

      return width;
    }
  },

  beforeMount () {
    this.init();
  },

  methods: {
    init: async function () {
      this.competitions = await api.get('competition');
    }
  }
};
</script>
