<template>
  <v-container fluid>
    <v-img class="mx-auto mt-4" max-width="180" max-height="148" src="https://i.imgur.com/ZbZNLoW.png" />

    <PageTitle title="臺科普類盃" />

    <Divider />

    <ProgressCircular v-if="!news"/>
    <div v-else>
      <SectionTitle title="最新消息" align="center" />
      <CardText
        v-for="newsData in news" :key="newsData.id"
        :imageIcon="'mdi-newspaper-variant'"
        :cardTitle="newsData.title"
        :cardMainText="newsData.content"
        :cardSubText="newsData.date"
      />
    </div>

    <ProgressCircular v-if="!links"/>
    <div v-else>
      <SectionTitle title="常用連結" align="center" />
      <CardLink
        v-for="link in links" :key="link.id"
        :imageIcon="link.icon"
        :cardLink="link.url"
        :cardTitle="link.name"
      />
    </div>

  </v-container>
</template>

<script>
import api from '@/lib/api';

export default {
  name: 'Home',

  data () {
    return {
      news: null,
      links: null
    };
  },

  beforeMount () {
    this.init();
  },

  methods: {
    init: async function () {
      this.news = (await api.get('news')).reverse();
      this.links = (await api.get('link'));
    }
  }
};
</script>
