      <v-btn text to="/home" @click="drawer = true">
        <v-icon class="mx-4" left large dark>mdi-volleyball</v-icon>
        <v-toolbar-title>臺科普類盃</v-toolbar-title>
      </v-btn>
      <v-spacer></v-spacer>
    </v-app-bar>

    <v-navigation-drawer :color="color.panel.grey" width="170" v-model="drawer" app clipped disable-resize-watcher>
      <v-list nav>
