<script>
import color from '@/lib/color';

export default {
  name: 'App',

  data () {
    return {
      color: color,
      drawer: null,
      items: [
        { heading: '賽事' },
        { icon: 'mdi-history', text: '歷史沿革', url: 'history' },
        { icon: 'mdi-file-document-multiple', text: '競賽章程', url: 'charter' },
        { icon: 'mdi-account-plus', text: '線上報名', url: 'signup' },
        { icon: 'mdi-cart', text: '商品專區', url: 'store' },
        { icon: 'mdi-bullhorn', text: '賽事公告', url: 'competition' },
        { divider: true },
        { heading: '問答' },
        { icon: 'mdi-progress-question', text: '關於報名', url: 'qa/signup' },
        { icon: 'mdi-progress-question', text: '關於比賽', url: 'qa/competition' },
        { icon: 'mdi-progress-question', text: '關於商品', url: 'qa/store' },
        { divider: true },
        { heading: '關於' },
        { icon: 'mdi-account-star', text: '主辦單位', url: 'organizer' },
        { icon: 'mdi-account-heart', text: '協同合作', url: 'cooperation' }
      ]
    };
  },

  created () {
    this.$vuetify.theme.dark = true;
    this.drawer = this.defaultDrawer();
  },

  methods: {
    defaultDrawer () {
      let drawer = null;

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          drawer = false;
          break;
        case 'sm':
          drawer = false;
          break;
        case 'md':
          drawer = true;
          break;
        case 'lg':
          drawer = true;
          break;
        case 'xl':
          drawer = true;
          break;
      }

      return drawer;
    }
  }
};
</script>

